export default {
  async asyncData (vm) {
    /*
    * vm.params带有一个参数：
    * id 新闻id
    * vm.query带有一个参数：
    * type 新闻/案例
    * */
    let detailRes, detail
    if (vm.query.type === 'case') {
      detailRes = await vm.$requests.entityCaseDetail({
        case_id: vm.params.id
      })
    } else {
      detailRes = await vm.$requests.entityNewsDetail({
        news_id: vm.params.id
      })
    }
    if (detailRes.status === 200 && detailRes.data.result === 'success') {
      detail = detailRes.data.data
    }
    return {
      detail
    }
  }
}
